import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { SliderComponent } from './slider/slider.component';
import { ScheduleComponent } from './schedule/schedule.component';
import { SpeakersComponent } from './speakers/speakers.component';
import { GalleryComponent } from './gallery/gallery.component';
import { SignupComponent } from './signup/signup.component';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { ApiService } from './shared/api.service';
import { ConfigService } from './shared/config.service';
import { DownloadComponent } from './download/download.component';
import { FooterComponent } from './footer/footer.component';
import { RouterModule } from '@angular/router';
import { SpeakersPipe } from './shared/pipes/speakers.pipe';
import { NgHttpLoaderModule } from 'ng-http-loader';
// import {SlickModule} from 'ngx-slick';
import { MyAccountComponent } from './my-account/my-account.component';
import { MasterComponent } from './master/master.component';
import { PaginatorModule } from 'primeng/primeng';
import { MyErrorHandler } from './handle-error';
import { ToastModule } from 'primeng/toast';
import { MessageService, ConfirmationService } from 'primeng/api';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SimpleSmoothScrollModule } from 'ng2-simple-smooth-scroll';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';
import { SlickCarouselModule } from 'ngx-slick-carousel';
import { AutosizeModule } from 'ngx-autosize';
import { TabViewModule } from 'primeng/tabview';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    SliderComponent,
    ScheduleComponent,
    SpeakersComponent,
    GalleryComponent,
    SignupComponent,
    DownloadComponent,
    FooterComponent,
    SpeakersPipe,
    MyAccountComponent,
    MasterComponent,
    ForgotPasswordComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    PaginatorModule,
    ToastModule,
    SlickCarouselModule,
    AutosizeModule,
    TabViewModule,
    ScrollToModule.forRoot(),
    SimpleSmoothScrollModule,
    // SlickModule.forRoot(),
    RouterModule.forRoot(
      [
        { path: 'home', component: MasterComponent },
        { path: 'myAccount', component: MyAccountComponent },
        { path: 'forget-password', component: ForgotPasswordComponent },
        { path: '**', component: MasterComponent }
      ],
      {
        useHash: false,
        anchorScrolling: "enabled",
        onSameUrlNavigation: "reload",
        enableTracing: false,
        scrollPositionRestoration: "enabled"
      }
    ),
    NgHttpLoaderModule.forRoot()
  ],
  providers: [{ provide: ErrorHandler, useClass: MyErrorHandler }, { provide: LocationStrategy, useClass: HashLocationStrategy }, ApiService, MessageService, ConfigService],
  bootstrap: [AppComponent]
})
export class AppModule { }
