import { Component, OnInit } from '@angular/core';
import { ApiService } from '../shared/api.service';
import { ScheduleModel } from './schedule-model';
import { SpeakerModel } from '../speakers/speaker-model';
import { fadeIn } from 'ng-animate';
import { trigger, transition, useAnimation } from '@angular/animations';

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.css'],
  animations: [
    trigger('fadeIn', [transition(':enter', useAnimation(fadeIn, {
      params: { timing: 1 }
    }))]),
  ]
})
export class ScheduleComponent implements OnInit {
  schedule: ScheduleModel[] = [];
  speakers: SpeakerModel[] = [];
  slideConfig2 = { "slidesToShow": 1, "slidesToScroll": 1, infinite: false, dots: true};
  slideConfig3 = { "slidesToShow": 1, "slidesToScroll": 1, infinite: false, dots: true};
  active: any = 1;
  constructor(private apiService: ApiService) {
  }

  ngOnInit() {
    this.initSchedule();
    this.initSpeakers();
  }

  initSchedule() {
    this.apiService.getSchedule().subscribe(res => {
      this.schedule = res;
      // console.log(this.schedule)
    });
  }
  getPath(id) {
    if (this.speakers.length > 0) {
      const speakers: SpeakerModel[] = this.speakers.filter(x => x.id == id);
      return speakers[0].imagePath;
    }
  }
  getName(id) {
    if (this.speakers.length > 0) {
      const speakers: SpeakerModel[] = this.speakers.filter(x => x.id == id);
      return speakers[0].name;
    }
  }
  initSpeakers() {
    this.apiService.getSpeakers().subscribe(res => {
      this.speakers = res;
    });
  }
}
