import {Component, ElementRef, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import {ApiService} from '../shared/api.service';
import {forEach} from '@angular/router/src/utils/collection';

declare let $: any;

@Component({
    selector: 'app-gallery',
    templateUrl: './gallery.component.html',
    styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
    public allImages: any[] = [];
    public config: any;
    public imagesList: any[];
    constructor(public callApi: ApiService) {
    }

    ngOnInit() {

        this.callApi.getGallery().subscribe((res) => {
            this.allImages = res.data;
            this.imagesList =  this.allImages.slice(0,6);
        });

        this.config = {
            dots: false,
            infinite: true,
            speed: 500,

            slidesToShow: 1,
            slidesToScroll: 1,


            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        infinite: true,
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }

            ]

        };

    }

    pages(index) {
        let next = index*6;
        this.imagesList = this.allImages.slice((next-6),next);
    }
}
