import { Injectable } from '@angular/core';
import {ScrollToConfigOptions, ScrollToService} from '@nicky-lenaers/ngx-scroll-to';

@Injectable({
  providedIn: 'root'
})
export class MyScrollServiceService {

  constructor(private _scrollToService: ScrollToService) {
  }

  public triggerScrollTo(url, pervUrl) {
    console.log(url, pervUrl)
    let _config: ScrollToConfigOptions = {
      target: url,
      duration: 1050,
      easing: 'easeOutElastic',
      offsetMap: new Map()
    };

    if (url == pervUrl && pervUrl == 'home') {
      _config.offset = 0;
    } else if (url != pervUrl && pervUrl == 'home') {
      _config.offsetMap.set(300, -450);
      _config.offsetMap.set(767, -230);
      _config.offsetMap.set(1023, -50);
      // _config.offsetMap.set(1920, -78);
      // _config.offsetMap.set(1240, 0);

    }
    else if (url != pervUrl && pervUrl == null) {
      _config.offsetMap.set(300, -50);
      _config.offsetMap.set(767, -230);
      _config.offsetMap.set(1023, -50);
      // _config.offsetMap.set(1240, 0);
      // _config.offsetMap.set(1920, -78);
    } else {
      _config.offset = 1;
      _config.offsetMap.set(767, -10);

    }

    this._scrollToService.scrollTo(_config);
  }

}
