import { Component, OnInit } from '@angular/core';
import { ApiService } from '../shared/api.service';
import { ActivatedRoute, Route } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-my-account',
  templateUrl: './my-account.component.html',
  styleUrls: ['./my-account.component.css']
})
export class MyAccountComponent implements OnInit {
  activeTab: any = 1;
  public allQuestions: any;
  public myQuestion: any;
  public doctor_id: any;
  public showAlert: boolean = false;
  _alert: any = {};
  //vote
  public voteInfo: any;
  public voteAnswer: any;
  isActive: number = 5;
  public waiting: any;
  public sourceImage: any;
  public voteResults: any;
  private totalCount: number;
  timeLeft: number =60;
  interval;
  highRate: any = {};
  constructor(public api: ApiService, public router: ActivatedRoute, public sanitizer: DomSanitizer) {
    // this.router.queryParamMap.subscribe(params => {
    //   this.activeTab = params.get('voting');
    // });
  }

  ngOnInit() {
    this.loadAllQuestions();
    let userInfo = JSON.parse(localStorage.getItem('userInfo'));
    this.doctor_id = userInfo.user.id;
    setInterval(() => {
      this.loadAllQuestions();
    }, 300000);
   
  }

  loadAllQuestions() {
    this.api.getAllQuestions().subscribe((res) => {
      this.allQuestions = res.data;
      console.log(this.allQuestions);
    });
  }
//   startTimer() {
//     if(this.voteInfo.inProcess==0||(this.voteInfo.isAnswered==0&&this.voteInfo.sendToVote==1&&this.voteInfo.startVote==1)){
//     this.interval = setInterval(() => {
//       if(this.timeLeft > 0) {
//         this.timeLeft--;
//       } else {
//      this.waiting == 'Found';
//         // this.timeLeft = 0;
//       }
//     },1000)
//   }
// }
  sendQuestion(data) {
    if (data.invalid) {
      this._alert = {
        message: 'Invalid username or password.',
        title: 'Failed!',
        success: false
      };
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false;
      }, 3000);
      return;
    }
    let _question = { doctor_id: this.doctor_id, name: data.value.name };
    console.log(_question);
    this.api.setQuestion(_question).subscribe((res) => {
      this.loadAllQuestions();
      this._alert = {
        message: 'Successfully Sent',
        title: 'Success!',
        success: true
      };
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false;
      }, 3000);
      data.reset();
    });
  }

  getCurrentVote() {
    this.api.getVote().subscribe((res) => {
      this.voteInfo = res.data;
      this.checkVote();

    });
  }

  myVote() {
    if (this.voteAnswer) {
      console.log(this.voteAnswer)
      console.log(this.voteInfo.id)
      let voteObj = { answer_id: this.voteAnswer, question_id: this.voteInfo.id, doctor_id: this.doctor_id };
      this.api.setVote(voteObj).subscribe((res) => {
        console.log(res)
        // this.voteInfo = res.data;
        this.waiting = 'Found';
        this._alert = {
          message: 'Successfully Voted',
          title: 'Success!',
          success: true
        };
        this.showAlert = true;
        setTimeout(() => {
          this.showAlert = false;
        }, 3000);
      });
    }
  }

  checkVote() {
    console.log(this.voteInfo);
    if(this.voteInfo.inProcess==0||(this.voteInfo.isAnswered==0&&this.voteInfo.sendToVote==1&&this.voteInfo.startVote==1)){
      this.interval = setInterval(() => {
        if(this.timeLeft > 0) {
          this.timeLeft--;
        } else {
       this.waiting = 'Found';
          // this.timeLeft = 0;
        }
      },1000)
    }
    if (this.voteInfo && this.voteInfo.id) {
      this.api.getCheckVote({ question_id: this.voteInfo.id, doctor_id: this.doctor_id }).subscribe((res) => {
        this.waiting = res.success;
        this.getResults();
      });
    } else {
      this.waiting = null;
    }

  }

  getQr() {
    let myQr = JSON.parse(localStorage.getItem('userInfo'));
    this.api.getMyQr(myQr.user.id).subscribe((res) => {
      this.sourceImage = this.sanitizer.bypassSecurityTrustResourceUrl('data:Image/*;base64,' + res.data);
    });
  }

  getResults() {
    this.api.getVoteResults().subscribe((res) => {
      console.log("reults:", res.data)
      this.voteResults = res.data;
      this.totalCount = 0;
      if (this.voteResults) {
        this.highRate = this.voteResults.count_answers[0];
        this.voteResults.count_answers.forEach((e, n) => {
          this.voteResults.question.answers.forEach((x, i) => {
            if (x.id == e.answer_id) {
              // console.log(x)
              x.count = e.count;
              this.totalCount = this.totalCount + e.count;
              if (e.count > this.highRate.count) {
                this.highRate = e;
              }
            } else if (!x.count) {
              x.count = 0;
            }

          });
        });

      }
    });
  }
}
