import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiService } from '../shared/api.service';
import { NavigationEnd, Router } from '@angular/router';
import { NgForm } from '@angular/forms';

declare let $: any;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  showAlert = false;
  _userInfo: any = null;
  alert: {
    success?: boolean;
    title?: string
    message?: string;
  } = {};
  user_id: any;
  doctor_id: any;
  @ViewChild('signUpForm') signupForm: NgForm;
  // @ViewChild('signUpForm') loginForm: NgForm;
  inAccount: boolean = false;
  constructor(private apiService: ApiService, private router: Router) {
  }

  ngOnInit() {
    let _data = localStorage.getItem('userInfo');
    this._userInfo = JSON.parse(_data);
    // this.loginForm.reset();
  }

  onSignUp(signUpForm) {
    const obj = { ...signUpForm.value, userType: 1, event_id: 1 };
    this.apiService.signUp(obj).subscribe(res => {
      if (res.success == false) {
        res.message.forEach(e => {
          this.alert = {
            message: e,
            title: 'Failed!',
            success: false
          };
          this.showAlert = true;
        });
        // this.alert = {
        //   message: res.message[0],
        //   title: 'Failed!',
        //   success: false
        // };
        // this.showAlert = true;

        setTimeout(() => {
          this.showAlert = false;
        }, 3000);
      } else if (res.status == 200) {
        this.doctor_id = res.user.id;
        this.user_id = res.user.id;
        this.alert = {
          message: 'Successfully Registered',
          title: 'Success!',
          success: true
        };
        this.showAlert = true;

        setTimeout(() => {
          this.showAlert = false;
          $('#myModal').modal('hide');
          $('#DocInfo').modal('show');

        }, 1000);

        this._userInfo = res;
        localStorage.setItem('userInfo', JSON.stringify(res));
        localStorage.setItem('token', res.access_token);
        this.router.navigate(['/home']);
        $('.navbar-collapse').collapse('hide');
        signUpForm.reset();
      }
    });
  }

  goToLocation(fragment) {
    $('#myModal').modal('hide');
    $('.navbar-collapse').collapse('hide');
    this.router.navigate(['./'], { fragment: fragment });
  }

  goToMyAccount() {
    this.router.navigate(['/myAccount']);
    $('.navbar-collapse').collapse('hide');
  }
  // goToForgetPass(){
  //   this.router.navigate(['/forget-password']);
  //   $('.navbar-collapse').collapse('hide');

  // }

  openSignup() {
    this.signupForm.reset();
    $('#myModal').modal('show');
    $('.navbar-collapse').collapse('hide');
  }

  onLogin(data) {
    if (data.invalid) {
      this.alert = {
        message: 'Invalid username or password.',
        title: 'Failed!',
        success: false
      };
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false;
      }, 3000);
      return;
    }
    // let userData = {...data.value, userType: 1, event_id: 1};
    // console.log(data.value.email);
    this.apiService.login({ email: data.value.email, password: data.value.password }, 1).subscribe((res) => {
      this._userInfo = res;
      console.log(res);
      localStorage.setItem('userInfo', JSON.stringify(res));
      localStorage.setItem('token', res.access_token);
      this.doctor_id = res.user.id;
      this.user_id = res.user.id;
      this.alert = {
        message: 'Successful Login',
        title: 'Success!',
        success: true
      };
      this.showAlert = true;
      this.router.navigate(['/home']);
      $('.navbar-collapse').collapse('hide');
      data.reset();
      setTimeout(() => {
        this.showAlert = false;
        $('#myModal2').modal('hide');

      }, 3000);
    });
  }

  logout() {
    this.apiService.logOut({ id: this._userInfo.user.id }).subscribe(res => {
      console.log(res);
      localStorage.removeItem('userInfo');
      localStorage.removeItem('token');
      this._userInfo = null;
      this.goToLocation("home");
      // this.router.navigate(['/']);
    })

  }

  onAddDoctor(doctorForm) {
    const token = localStorage.getItem('token');
    console.log('token', token);
    const obj = { ...doctorForm.value, event_id: 1, userType: 1, doctor_id: this.doctor_id, user_id: this.user_id };
    this.apiService.registerDoctor(obj).subscribe(res => {
      if (res.status == 200) {
        this.alert = {
          message: 'Successfully Registered',
          title: 'Success!',
          success: true
        };
        this.showAlert = true;
        setTimeout(() => {
          this.showAlert = false;
          $('#DocInfo').modal('hide');
          if (token) {
            this.router.navigate(['/home']);
          } else {
            $('#myModal2').modal('show');
          }
        }, 2000);
      }
    });
  }

  ngAfterViewInit() {

    this.router.events.subscribe(s => {
      if (s instanceof NavigationEnd) {
        let _this = this;

        if (s.url.indexOf('myAccount') > -1) {
          this.inAccount = true;
        } else {
          this.inAccount = false;
        }
      }
    });
  }



}
