import { Injectable, ErrorHandler, Injector } from '@angular/core';
import {MessageService} from 'primeng/api';

@Injectable()
export class MyErrorHandler implements ErrorHandler {

  constructor(private injector: Injector, private messageService: MessageService) {

  }


  public handleError(error: any) {
    console.log('code', error.status)
    //const router = this.injector.get();
    const httpErrorCode = error.status;
    switch (httpErrorCode) {
      case 422:
        this.showError(error.error.message || error.error.error || error.message );
        // this.router.navigateByUrl("/login");
        break;
      case 401:
        this.showError(error.error.message || error.error.error || error.message);
        localStorage.removeItem('token');
        // this.storage.clear();
        //router.navigate(['/login']);
        // this.showError(error.error.message || error.error.error || error.message);

        break;
      case 402:
        this.showError(error.error.message || error.error.error || error.message);
        break;
      case 0:
        this.showError("Internal Server Error.");
        break;
    }
  }

  private async showError(message: string) {
    this.messageService.add({severity: 'error', summary: 'Error Message', detail: message});

    //this.toastManager.add({severity: 'error', summary:  'Something went wrong', detail: message});
    // const toast = await this.toastController.create({
    //   message: message,
    //   showCloseButton: false,
    //   position: 'bottom',
    //   duration: 2000
    // });
    // toast.present();2
  }
}
