import { Component, OnInit } from '@angular/core';
import { ApiService } from '../shared/api.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';

interface passwordInfo {
  email: string,
  code: string,
  password: string
}

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  showreset = false;
  passwordData: passwordInfo;

  constructor(private apiService: ApiService, public router: Router, public route: ActivatedRoute) { }

  ngOnInit() {
    if (this.route.snapshot.queryParamMap.has('code') && this.route.snapshot.queryParamMap.has('email')) {
      this.showreset = true;
    }else{

    this.showreset = false;
  }
  }
  resetPass(passwordForm:NgForm) {
    this.passwordData = {
      email: this.route.snapshot.queryParamMap.get('email'),
      code: this.route.snapshot.queryParamMap.get('code'),
      password: passwordForm.value.password
    }
    this.apiService.setNewForgetPassword(this.passwordData).subscribe(res => {
      if (res.status == 200) {
        setTimeout(() => {
          this.router.navigate(['/login']);
          passwordForm.reset();
        }, 2000);
      }
      passwordForm.reset();
    });
  }

}
